










import Record from 'caml-js/Record';
import dayOfYear from 'time-js/dayOfYear';
import {
    Component,
    Vue,
    Prop,
} from 'vue-property-decorator';
@Component
export default class App extends Vue {
    @Prop() readonly records!: Record[];
    @Prop() readonly year!: number;
    @Prop() readonly day!: number;
    get duration() {
        let result = 0;
        for(const record of this.records) {
            const duration = record.annotations.find((annotation: any) => {
                return annotation.key === 'duration';
            });
            if(!duration) {
                continue;
            }
            result += Math.ceil(
                parseInt(duration.value) / 3600
            );
        }
        return result;
    }
    get isToday() {
        const now = new Date();
        if(now.getFullYear() !== this.year) {
            return false;
        }
        const today = dayOfYear(
            now.getFullYear(),
            now.getMonth(),
            now.getDate(),
        );
        if(today !== this.day) {
            return false;
        }
        return true;
    }
}
